/* Reset CSS  */
html {
  overflow: scroll;
  overflow-x: hidden;
}

/* Optional: show position indicator in red */


*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;

}


/* Root styling making flexbox */
.main {
  display: flex;
  flex-direction: column;
  
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 12px;
  background-color: #fafafa;
  width: 100%;
  flex-wrap: nowrap;
  
  
}
.dark {
  background-color: black;
}
/* Navbar sytling  */
nav {
  height: 80px;
  background-color: #2B7FC3;
  display: flex;
  flex-direction: row;
  position: relative;
  box-shadow: 2px 2px #2B7FC3;
}
form {
float: right;
position: absolute;
right: 40px;
top :10px
}

nav h2 {
  font-size: 25px;
  font-weight: bold;
  color: #fafafa;
  margin-top: 20px;
  margin-left: 50px;
 }
 input {
  width: 250px;
  height: 50px;
  border-radius: 5px;
  outline: none;
  font-size: 16px;
  background-color: #fafafa;
  text-align: start;
  padding: 10px;
}
button[type=submit] {
height: 40px;
font-size: 16px;
width: 100px;
padding: 5px;
cursor: pointer;
border-color: none;
border-radius: 5px;
background-color: #fafafa;
margin-left: 10px;
}

 
@media only screen and (max-width: 768px) {
  nav {
    height: 90px;
    background-color: #2B7FC3;
    
    
  }
  nav h2 {
    font-size: 20px;
    font-weight: bold;
    color: #fafafa;
    width: 100%;
    text-align: center;
    margin-top:10px;
   }
   nav h2 img {
     width: 120px;
   }
form {
  margin-top: 30px;
  width: 90%;
}
button [type=submit] {
  width:65px;
  height: 20px;
}
input {
  margin-left: 20px;
  width: 193px;
  height: 30px;
}

}



button:hover {
  color: blue;
}

/* Card body styling  */
.card-body{
  border:2px solid black;
  margin:20px;
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  max-width: 2000px;
  border-radius: 5px;
  overflow: hidden;
  gap: 1rem;
  background: #212c47;
  box-shadow: 2px 3px 4px rgba(0, 0, 0, 0.3);
  color: #ccc;
}
.title {
  font-size: 2rem;
  color: #fff;
  text-transform: capitalize;
  margin-left: 20px;
}
img {
  margin-left: 20px;

}
.desc , .subtitle, .type , .type + div ,.apply , a {
  color: #ccc;
  margin-top: 1.5rem;
  font-size:30px;
  letter-spacing: 2px;
  font-weight: normal;
  margin-left: 20px;
}
p{
  color: #ccc;
  margin-top: 20px;
  margin-bottom: 10px;
  
  
}
.btn {
  height: 40px;
  font-size: 20px;
  cursor: pointer;
}

/* for mobile  */
@media screen and (max-width: 768px) {
  .card-body {
    grid-template-columns: 1fr;
    margin: 1.5rem;
    gap:0.5 rem;

  }
  .title {
    font-size: 15px;
  }
  .desc , .subtitle, .type , .type + div ,.apply , a {
    color: #ccc;
    margin-top: 0.1rem;
    font-size:10px;
    letter-spacing: 1.5px;
    font-weight: normal;
    margin-left: 20px;
    text-overflow: ellipsis;

  }
  p{
    color: #ccc;
    margin-top: 20px;
    margin-bottom: 10px;
    
    
  }
  
}

/* Footer Styling  */

.footer {
  background-color: black;
  color: white;
 height: 80px;
 text-align: center;
 font-size: 2rem;
 padding: 20px;
}
@media only screen and (max-width: 768px){
  .footer {
    padding: 10px;
  font-size: 15px;

  }
}

.heart {
  color: red;
}
.btnd {
  background-color: black;
  color: wheat;
  width: 100px;
  display: block;
  margin: auto;
  height: 50px;
  font-size: 20px;
  border: 2px solid white;
  border-radius: 5px;
  cursor: pointer;
}
.btnl {
  background-color: white;
  color:black;
  border-radius: 5px;
  width: 100px;
  display: block;
  margin: 10px auto;
  height: 50px;
  font-size: 20px;
  cursor: pointer;
}